import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController} from 'ionic-angular';

import { EditarplatilloPage } from '../editarplatillo/editarplatillo';
import { AddplatilloPage } from '../addplatillo/addplatillo';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
//import { Observable } from 'rxjs/Observable';


interface ToDo{
  task: string;
  priority: number;
  id?: string;
}

@IonicPage()
@Component({
  selector: 'page-ctrlplatillos',
  templateUrl: 'ctrlplatillos.html',
})
export class CtrlplatillosPage {
  /*
  platilloCollection: AngularFirestoreCollection<Platillos>;
  platillos: Observable<Platillos[]>;
  */
  todoCollection: AngularFirestoreCollection<ToDo>;
  todo: ToDo[];
  canReorder: boolean = false;

  constructor(private alertCtrl: AlertController, private asf: AngularFirestore,
    public navCtrl: NavController, public navParams: NavParams) {

  }
    ionViewDidEnter(){
    this.todoCollection = this.asf.collection('todo', ref => ref.orderBy('priority'));
    this.todoCollection.snapshotChanges().subscribe(todoList => {
      this.todo = todoList.map(item => {
        return{
          task: item.payload.doc.data().task,
          priority: item.payload.doc.data().priority,
          id: item.payload.doc.id
        }
      })
    })
  }

  reorderItems(indexes: any){
    if (this.canReorder) {
      // Lote de procesos
      let batch = this.asf.firestore.batch();

      //Movemos elementos
      let element = this.todo[indexes.from];
      this.todo.splice(indexes.from, 1);
      this.todo.splice(indexes.to, 0, element);

      // Agregamos updates al batch
      this.todo.forEach((item: ToDo, index: number) =>{
        if (item.priority != index) {
          let ref = this.asf.doc(`todo/${item.id}`).ref;
          batch.update(ref, { priority: index });
        }
      });
      
      //Ejecutamos lote de procesos
      batch.commit().then(() => {
        console.log('Lista de tareas reordenadas');
      }).catch(err => {
        console.error(err);
      });
    }
  }
  deleteItem(item: ToDo){
    this.asf.doc(`todo/${item.id}`).delete().then(() => {
      console.log(`Tarea eliminada: "${item.task}"`);
    }).catch(err => {
      console.log(err);
    })
  }
  newItem(){
    let prompt = this.alertCtrl.create({
      title: 'Añadir Platillo',
      message: 'Indica la nueva tarea a añadir',
      inputs: [{
        name: 'task',
        placeholder: 'Nuevo platillo... '
      },{
        name: 'precio',
        placeholder: 'Precio... '
      }],
      buttons:[{
        text: 'Cancelar'
      }, {
        text: 'Guardar',
        handler: data =>{
          this.addTask(data.task, data.precio);
        }
      }]
    }).present();
  }
  addTask(task: string, precio: number){
    let priority = 0;
    if ( this.todo.length > 0) {
      let last = this.todo.length - 1;
      priority = this.todo[last].priority + 1;
    }
    this.asf.collection('todo').add({ task, precio, priority }).then(newItem => {
      //console.log(`Nueva tarea: "${task}" (ID: ${this.newItem.id})`);
    }).catch(err => {
      console.error(err);
    })
  }
  
  /*
  ionViewDidEnter(){
    this.todoCollection = this.asf.collection('todo');
    this.todo = this.todoCollection.valueChanges();
  }
*/
  Editar(){
    this.navCtrl.push(EditarplatilloPage);
  }
  Agregar(){
    this.navCtrl.push(AddplatilloPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CtrlplatillosPage');
  }


}



