import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AngularFirestore, AngularFirestoreCollection,
  AngularFirestoreDocument } from 'angularfire2/firestore';

interface Ordenes{
  platillo: string;
  nota: string;
  cantidad: number;
  mesa: number;
  precio: number;
  comensales: number;
  id?: string;
}

@IonicPage()
@Component({
  selector: 'page-cuentamesa',
  templateUrl: 'cuentamesa.html',
})
export class CuentamesaPage {
  ordenesCollection: AngularFirestoreCollection<Ordenes>;
  ordenes: Ordenes[];
  mesa = 0;
  total = 0;
  constructor(private asf: AngularFirestore, 
    public navCtrl: NavController, public navParams: NavParams) {
      this.mesa = this.navParams.get('mesa');
      console.log('Mesa:'+ this.mesa);
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad CuentamesaPage');
    this.ordenesCollection = this.asf.collection('ordenes', ref => 
    ref.where('mesa', '==', this.mesa));

    this.ordenesCollection.snapshotChanges().subscribe(ordenesList => {
      this.ordenes = ordenesList.map(item => {
        this.total = this.total + item.payload.doc.data().precio;
        return{
          nota: item.payload.doc.data().nota,
          platillo: item.payload.doc.data().platillo,
          comensales: item.payload.doc.data().comensales,
          cantidad: item.payload.doc.data().cantidad,
          mesa: item.payload.doc.data().mesa,
          precio: item.payload.doc.data().precio,
          id: item.payload.doc.id
        }
      })
    })
  }

}



