import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { CtrlplatillosPage } from '../ctrlplatillos/ctrlplatillos';

/**
 * Generated class for the EditarplatilloPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-editarplatillo',
  templateUrl: 'editarplatillo.html',
})
export class EditarplatilloPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  irPlatillos(){
    this.navCtrl.push(CtrlplatillosPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditarplatilloPage');
  }

}



