import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { AdministradorPage } from '../pages/administrador/administrador';
import { UsuariosPage } from '../pages/usuarios/usuarios';
import { AddusersPage } from '../pages/addusers/addusers';
import { UpdateusersPage } from '../pages/updateusers/updateusers';
import { AddplatilloPage } from '../pages/addplatillo/addplatillo';
import { CtrlplatillosPage } from '../pages/ctrlplatillos/ctrlplatillos';
import { CtrlcobroPage } from '../pages/ctrlcobro/ctrlcobro';
import { CuentamesaPage } from '../pages/cuentamesa/cuentamesa';
import { EditarplatilloPage } from '../pages/editarplatillo/editarplatillo';
import { RegistroPage } from '../pages/registro/registro';
import { ProfilePage } from '../pages/profile/profile';

import { CtrlcortecPage } from '../pages/ctrlcortec/ctrlcortec';
import { DetallecortecPage } from '../pages/detallecortec/detallecortec';

import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';

import { FIREBASE_CONFIG } from './app.firebase.config';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFireDatabaseModule } from 'angularfire2/database';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    RegistroPage,
    AdministradorPage,
    UsuariosPage,
    AddusersPage,
    UpdateusersPage,
    CtrlplatillosPage,
    AddplatilloPage,
    EditarplatilloPage,
    CtrlcobroPage,
    CuentamesaPage,
    CtrlcortecPage,
    DetallecortecPage,
    ProfilePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    AngularFirestoreModule.enablePersistence()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    RegistroPage,
    AdministradorPage,
    UsuariosPage,
    AddusersPage,
    UpdateusersPage,
    CtrlplatillosPage,
    AddplatilloPage,
    EditarplatilloPage,
    CtrlcobroPage,
    CuentamesaPage,
    CtrlcortecPage,
    DetallecortecPage,
    ProfilePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



