import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { UsuariosPage } from '../usuarios/usuarios';
import { LoginPage } from '../login/login';
import { CtrlplatillosPage} from '../ctrlplatillos/ctrlplatillos';
import { AngularFireAuth } from 'angularfire2/auth';
import { CtrlcobroPage } from '../ctrlcobro/ctrlcobro';
import { CtrlcortecPage} from '../ctrlcortec/ctrlcortec';
import { ProfilePage } from '../profile/profile';
import{ RegistroPage } from '../registro/registro';
@IonicPage()
@Component({
  selector: 'page-administrador',
  templateUrl: 'administrador.html',
})
export class AdministradorPage {

  constructor(private afAuth: AngularFireAuth, private toast: ToastController,
    public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    this.afAuth.authState.subscribe(data => {
      if(data && data.email && data.uid){
        this.toast.create({
          message: `Bienvenido a R-Order, ${data.email}`,
          duration: 3000
        }).present();
      }
      else{
        this.toast.create({
          message: `No se encontro Usuario`,
          duration: 3000
        }).present();
      }
    });
  }
  abrirctrlplatillos(){
    this.navCtrl.push(CtrlplatillosPage);
  }
  salir(){
    this.navCtrl.setRoot(LoginPage);
  }
  abrirUsuarios(){
    this.navCtrl.push(UsuariosPage);
  }

  abrircobrom(){
    this.navCtrl.push(CtrlcobroPage);
  }
  abrircortec(){
    this.navCtrl.push(CtrlcortecPage);
  }
  abrirPerfil(){
    this.navCtrl.push(ProfilePage);
  }  
  registrar(){
    this.navCtrl.push(RegistroPage);
  }
}



