import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { AngularFirestore, AngularFirestoreCollection,
  AngularFirestoreDocument } from 'angularfire2/firestore';
import { CuentamesaPage } from '../cuentamesa/cuentamesa';

interface Mesa{
  numero: number;
  comensales: number;
  cuenta: number;
  id?: string;
}

@IonicPage()
@Component({
  selector: 'page-ctrlcobro',
  templateUrl: 'ctrlcobro.html',
})
export class CtrlcobroPage {
  mesaCollection: AngularFirestoreCollection<Mesa>;
  mesa: Mesa[];
  noMesa = 0;

  constructor(private alertCtrl: AlertController, private asf: AngularFirestore,
    public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CtrlcobroPage');
    this.cargarMesas();
  }
  verMesa(item: Mesa){
    this.noMesa = item.numero
    this.detalleMesa();
  }
  cargarMesas(){
    this.mesaCollection = this.asf.collection('mesa');
    this.mesaCollection.snapshotChanges().subscribe(mesaList => {
      this.mesa = mesaList.map(item => {
        return{
          numero: item.payload.doc.data().numero,
          cuenta: item.payload.doc.data().cuenta,
          comensales: item.payload.doc.data().comensales,
          id: item.payload.doc.id
        }
      })
    })
  }
    detalleMesa(){
      this.navCtrl.push(CuentamesaPage, {mesa: this.noMesa});
    }
    deleteMesa(item: Mesa){
      this.asf.doc(`mesa/${item.id}`).delete().then(() => {
        console.log(`Tarea eliminada: "${item.numero}"`);
      }).catch(err => {
        console.log(err);
      })
    }
  }




